@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

html {
  font-family: "Roboto", sans-serif;
}

body {
  background-image: url("/build/assets/images/background.webp");
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
  color: #ffffff;
}

img {
  width: 100%;
  height: auto;
}

.container {
  width: 60%;
  margin: 0 auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50px;
  margin: 0.3rem;
}
.header h1 {
  color: #f5deb3;
  font-weight: 900;
  margin: 1rem;
  transition: transform 0.3s ease;
}
.header h1:hover {
  transform: scale(1.1);
}

.nav a {
  color: #ffffff;
  font-weight: 700;
  margin: 1rem;
  cursor: pointer;
}
.nav a:hover {
  color: #f5deb3;
  transition: 0.2s ease-out;
}

.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 100vh;
  color: #ffffff;
}
.hero h2 {
  font-weight: 900;
  font-size: 4rem;
}
.hero p {
  font-size: 1.2rem;
}

.gallery {
  background-color: rgba(0, 0, 0, 0.7);
  text-align: center;
  font-size: 1.1rem;
  padding: 1.5rem;
  border-radius: 30px;
  margin-bottom: 9rem;
}
.gallery h2 {
  font-size: 3rem;
}
.gallery h2,
.gallery p {
  margin: 0.5rem;
  color: #ffffff;
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  margin-top: 15px;
}
.gallery-grid img {
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.2s ease;
}
.gallery-grid img:hover {
  transform: scale(1.1);
}

.overlay {
  align-items: center;
  background-color: rgba(0, 0, 0, 0.9);
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
}
.overlay img {
  display: block;
  width: 70%;
}

.contact h2 {
  text-align: center;
  font-size: 2.5rem;
  color: #ffffff;
  margin-bottom: 1rem;
}

.form {
  background-color: rgba(0, 0, 0, 0.7);
  width: min(40rem, 100%);
  margin: 0 auto;
  padding: 2rem;
  border-radius: 1rem;
}

.field {
  margin-bottom: 1rem;
}
.field textarea {
  height: 12rem;
}
.field label {
  color: #ffffff;
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.input-text {
  width: 100%;
  border: none;
  padding: 1.3rem;
  border-radius: 0.5rem;
  font-family: inherit;
  font-size: 100%;
}

.button {
  background-color: #998151;
  color: #ffffff;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  font-weight: bold;
  border-radius: 0.5rem;
  border: none;
  line-height: 1;
  cursor: pointer;
}
.button:hover {
  transition: 0.3s ease;
  transform: scale(1.1);
}

.align {
  display: flex;
  justify-content: center;
}

.footer {
  padding: 3rem 0;
  background-color: rgba(0, 0, 0, 0.7);
  margin-top: 5rem;
}
.footer p {
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
  color: #ffe8be;
}

@media only screen and (width <= 992px) {
  .container {
    width: 90%;
  }
  .gallery {
    padding: 1rem;
  }
  .gallery-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  .overlay img {
    width: 100%;
  }
  .form {
    width: min(30rem, 100%);
  }
}
@media only screen and (width <= 576px) {
  .header {
    flex-direction: column;
  }
  .header h1 {
    margin: 10px;
  }
  .nav {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-bottom: 10px;
  }
  .nav a {
    margin: 10px;
  }
  .container {
    width: 90%;
  }
  .gallery {
    padding: 1rem;
  }
  .gallery-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0.5rem;
  }
  .form {
    padding: 1.5rem;
  }
  .input-text {
    padding: 1rem;
  }
}